.homeview {
  .homeview-header {
    position: relative;
    height: 100vh;
    .play-vid,
    .down {
      position: absolute;
      transform: translate(-50%, -50%);
      left: 50%;
    }
    .play-vid {
      top: 50%;
      width: 60px;
      cursor: pointer;
    }
    .down {
      top: 95%;
      width: 40px;
    }
    .showcase {
      width: 100%;
      height: 100%;
      .hero {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: 40% top;
        vertical-align: middle;
      }
    }
  }
  .shopping-guide {
    background-color: #ffffff;
    padding: 100px 0;
    .container {
      width: 80%;
      max-width: 800px;
      margin: auto;
      .info {
        text-align: center;
        font-size: 40px;
      }
    }
  }
  .gender-guide {
    position: relative;
    height: 100vh;
    background-image: url('https://cdn.shopify.com/s/files/1/0654/5561/t/2/assets/home-list-2_1920x1100.jpg');
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    .gender-nav {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -30%);
      .gender-nav-btn {
        display: block;
        width: 280px;
        font-weight: bold;
        font-size: 25px;
        padding: 30px 30px;
        margin: 0 0 15px 0;
      }
    }
  }
  .featured-prds {
    padding: 100px;
    .container {
      text-align: center;
      width: 85%;
      margin: auto;
      .title {
        text-align: center;
        font-size: 35px;
        margin: 0 0 60px 0;
      }
      .prds-colln {
        display: grid;
        grid-template-columns: 33.3% 33.3% 33.3%;
        .prd {
          margin: 0 0 75px 0;
          cursor: pointer;
          .prd-title {
            text-align: center;
            font-size: 35px;
            font-weight: bold;
            margin: 0 0 50px 0;
          }
          .prd-img-container {
            width: 90%;
            margin: auto;
            .prd-img {
              width: 100%;
            }
          }
        }
      }
      .view-colln-btn {
        padding: 30px;
        width: 450px;
        text-align: center;
        background: #e4e4e4;
        font-weight: 600;
        font-size: 25px;
        margin: 50px 0 0 0;
      }
    }
  }
  .extra-showcase {
    height: 100vh;
    background-image: url('https://cdn.shopify.com/s/files/1/0654/5561/t/2/assets/home-slider-2_1920x1000.jpg');
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
  }
  .footer {
    height: 100vh;
    background-color: #333;
  }
}

@media (max-width: 1200px) {
  .homeview {
    .featured-prds {
      padding: 50px 40px;
      .container {
        width: 90%;
        .prds-colln {
          .prd {
            .prd-title {
              font-size: 25px;
            }
          }
        }
        .view-colln-btn {
          padding: 20px 0;
          width: 200px;
          font-size: 15px;
          margin: 50px 0 0 0;
        }
      }
    }
  }
}

@media (max-width: 1500px) {
  .homeview {
    .featured-prds {
      padding: 100px 50px;
      .container {
        width: 90%;
      }
    }
  }
}

@media (max-width: 900px) {
  .homeview {
    .featured-prds {
      padding: 50px 20px;
      .container {
        width: 95%;
        .prds-colln {
          display: grid;
          grid-template-columns: 50% 50%;
          .prd {
            .prd-title {
              font-size: 20px;
            }
          }
        }
      }
    }
  }
}

@media (max-width: 600px) {
  .homeview {
    .homeview-header {
      height: 80vh;
    }
    .gender-guide {
      .gender-nav {
        .gender-nav-btn {
          display: block;
          width: 200px;
          font-weight: bold;
          font-size: 17px;
          padding: 15px 15px;
          margin: 0 0 15px 0;
        }
      }
    }
  }
}

@media (max-width: 500px) {
  .homeview {
    .shopping-guide {
      padding: 80px 0;
      .container {
        .info {
          font-size: 25px;
        }
      }
    }
    .featured-prds {
      padding: 50px 10px;
      .container {
        width: 100%;
        .prds-colln {
          .prd {
            .prd-title {
              font-size: 17px;
            }
          }
        }
        .view-colln-btn {
          padding: 20px 0;
          width: 170px;
          font-size: 12px;
          margin: 50px 0 0 0;
        }
      }
    }
  }
}

@media (max-width: 380px) {
  .homeview {
    .shopping-guide {
      .container {
        .info {
          font-size: 20px;
        }
      }
    }
  }
}
